@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario {
    width: 100%;

    .campo-cita {
        display: flex;
        margin-bottom: 2rem;
        align-items: center;
        padding: 1rem;

        label {
            flex: 0 0 10rem;
        }

        input {
            flex: 1;
            border: none;
            padding: calc(v.$separacion / 4);
            border-radius: 1rem;

            &:disabled {
                background-color: lighten($color: v.$negro, $amount: 15%);
                color: v.$blanco;
                cursor: not-allowed;
            }
        }
    }

    .campo {
        width: 100%;
        height: 50px;
        position: relative;
        overflow: hidden;

        .campo-lbl {
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 100%;
            pointer-events: none;
            border-bottom: 1px solid #c7c7c7;
            color: #fff;

            &::after {
                content: "";
                position: absolute;
                left: 0;
                bottom: -1px;
                width: 100%;
                height: 100%;
                border-bottom: 2px solid #359ff7;
                transform: translateX(-100%);
                transition: all 0.3s ease;
            }

            span {
                font-size: 20px;
                position: absolute;
                bottom: 5px;
                left: 0;
                transition: all 0.3s ease;
            }
        }

        .campo-input {
            width: 100%;
            height: 100%;
            background: none;
            color: #fcfcfc;
            padding-top: 20px;
            border: none;
            outline: none;

            &:focus+.campo-lbl span,
            &:valid+.campo-lbl span {
                transform: translateY(-130%);
                font-size: 14px;
                color: #359ff7;
            }

            &:focus+.campo-lbl::after,
            &:valid+.campo-lbl::after {
                transform: translateX(0%);
            }
        }
    }
}