@use 'base/variables' as v;


.seccion {
    padding: 2.5rem 0;
    display: none;

    .text-center {
        text-align: center;
    }
}

.mostrar {
    display: block;
}

.ocultar {
    display: none;
}

.resumen {

    &.servicio {
        border-top: 2px solid v.$blanco;
        padding-top: 1rem;

        .precio {
            color: v.$azul;
            font-weight: v.$bold;
            font-size: 2rem;
        }
    }
}

.nombre {
    color: v.$azul;
    font-size: 2rem;
}

.nombreCliente {
    color: v.$azul;
    font-size: 2rem;
    font-weight: v.$bold;
}