@use 'base/variables' as v;
@use 'base/mixins' as m;


.listado-servicios {
    display: grid;
    gap: 2rem;
    padding: 3rem;

    @include m.tablet {
        @include m.grid(2, 2rem);
        padding: 1rem;
    }

    .servicio {
        background-color: v.$blanco;
        border-radius: 22px;
        padding: 1rem;
        transition: transform 300ms ease;
        text-align: center;
        cursor: pointer;

        &:hover {
            transform: scale(1.1);
        }

        p {
            margin: 1rem;
        }

        &.seleccionado {
            background-color: v.$azul;

            p {
                color: v.$blanco;
            }
        }
    }


    .nombre-servicio {
        color: v.$negro;

    }

    .precio-servicio {
        color: v.$azul;
        font-weight: v.$bold;
        font-size: 3rem;
    }
}