@use 'base/mixins' as m;

.tabla {
    display: block;
    width: 100%;
    overflow-x: scroll;

    @include m.desktop {
        padding: 0 2rem;
        display: table;
    }


    thead {
        tr {
            display: flex;
            justify-content: space-between;
        }
    }

    tbody {
        tr {
            display: flex;
            justify-content: space-between;
            align-items: center;
            font-size: 2.5rem;
            text-align: center;
            border-bottom: 1px solid rgb(165, 164, 164);

            td {
                flex: 1;
            }
        }
    }

}