@use 'base/variables' as v;
@use 'base/mixins' as m;



.contenedor-app {

    @include m.tablet {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        min-height: 100vh;
    }

    .imagen {
        height: 20rem;
        background-image: url(../img/1.webp);
        background-size: cover;
        background-position: center center;

        @include m.tablet {
            height: auto;
        }
    }

    .app {
        width: 100%;
        margin: 0 auto;
        padding: 2rem;

        @include m.tablet {
            overflow: overlay;
        }
    }



    .barra {
        margin: calc(v.$separacion) 0;
        text-align: center;

        @include m.telefono {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }

        .boton-rojo {
            background-color: v.$negro;
            padding: 1rem 2rem;
            width: auto;
            transition: all 500ms ease;

            &:hover {
                background-color: v.$rojo;
            }
        }
    }

    .barra-servicios {
        margin-bottom: v.$separacion;

        @include m.tablet {
            display: flex;
            justify-content: space-between;
            gap: 1rem;
        }

        .boton {
            margin: 0 0 1rem 0;
            padding: 1.5rem 1.5rem;
        }
    }
}