@use 'base/variables' as v;

.alerta-error {
    background-color: tomato;
    padding: 0.5rem 0;
    font-weight: v.$delgada;
    border-radius: 10px;
}

.alerta-exito {
    text-align: center;
    background-color: greenyellow;
    color: v.$negro;
    padding: 0.5rem 0;
    font-weight: v.$delgada;
    border-radius: 10px;
}

.alerta-borrar {
    display: none;
}