@use 'base/variables' as v;
@use 'base/mixins' as m;

.paginacion {
    padding: calc(v.$separacion /5);

    @include m.telefono {
        display: flex;
        justify-content: space-between;
        gap: 1rem;
    }


    button {
        margin: 0 0 1rem 0;

        &.ocultar {
            visibility: hidden;
        }
    }
}