@use 'base/variables' as v;
@use 'base/mixins' as m;

.acciones {
    margin-top: 5rem;

    @include m.tablet {
        display: flex;
        justify-content: space-between;
    }


    a {
        color: v.$blanco;
        display: block;
        margin-bottom: calc(v.$separacion/2);
        text-align: center;

        &:hover {
            color: v.$azul;
        }
    }
}